/* ARROWS */
.flex-next,
.zeus.tparrows {
  cursor:pointer;
  width:55px;
  height:80px;
  position:absolute;
  display:block;
  z-index:100;
  overflow:hidden;
  background: #fff;
}

.flex-next:before,
.zeus.tparrows:before {
  font-family: "fontawesome";
  font-size:36px;
  color:#333;
  display:block;
  line-height: 80px;
  text-align: center;    
  z-index:2;
  position:relative;
}

.zeus.tparrows.tp-leftarrow:before {
  content: "\f104";
  left: -1px;
}
.zeus.tparrows.tp-rightarrow:before {
  content: "\f105";
  right: -1px;
}

.zeus .tp-title-wrap {
	background: #0081db;
	width: 120%;
	height: 120%;
	top: 0;
	position: absolute;
	opacity: 0;
 }

.zeus .tp-title-wrap,
.tparrows:before {
	-webkit-transition: all 0.4s cubic-bezier(0.2, 1, 0.3, 1);
