/* BULLETS */
.hermes .tp-bullet {
    overflow:hidden;
    border-radius:50%;
    width:19px;
    height:19px;
    background-color: rgba(0, 0, 0, 0);
    box-shadow: inset 0 0 0 2px #FFF;
    -webkit-transition: background 0.3s ease;
